* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.principal {
    background-image: url('../resources/fondo.jpg');
    background-size: cover;
    height: 100vh;
    color: white;
    text-align: center;
}
.principal > img {
    width: 150px;
    height: 150px;
    margin-top: 50px;
}
.principal > button {
    width: 200px;
    height: 40px;
    margin-top: 75px;
    background-color: #5e454b;
    border: 2px solid #cee5d0;
    color: #cee5d0;
    font-size: 1.6em;
    cursor: pointer;
}
.principal > h1 {
    padding-top: 150px;
    font-size: 4em;
    letter-spacing: 4px;
}

@media only screen and (max-width: 1024px) {
    .principal > h1 {
        font-size: 2.5em;
    }
    .principal > img {
        width: 150px;
        height: 150px;
    }
}
